.leaderboard {
    width: 100%;
}

.leaderboard-header {
    width: 100%;
    text-align: left;
}

.leaderboard-header div {
    display: inline-block;
}

.leaderboard .data-row {
    width: 98%;
    padding: 1%;
    text-align: left;
    border-top: 1px solid black;
    font-size: 1.25em;
}

.odd-row {
    background-color: #dddddd;
}

.even-row {
    background-color: #cccccc;
}

.leaderboard .data-row div{
    display: inline-block;
}

.current-user {
    background-color: beige;
}

.leaderboard .rank {
    display: inline-block;
    width: 15%;
}
.leaderboard .name {
    display: inline-block;
    width: 65%;
}
.leaderboard .score {
    display: inline-block;
    width: 20%;
}

.leaderboard img {
    width: 0.75em;
    padding: 0.125em;
}

